import { buttonVariations } from './helpers';

buttonVariations.forEach((element) => {
	wp.blocks.registerBlockStyle('core/button', element);
});

wp.hooks.addFilter('blocks.registerBlockType', 'core/button', (props, name) => {
	if ('core/button' !== name) {
		return props;
	}

	props = Object.assign(props, {
		parent: [],
	});

	return props;
});
